const data = [
  {
    title: 'Pricing plans',
    paragraph
  },
  {
    title: 'How to apply',
    paragraph
  },
  {
    title: 'Purchasing process',
    paragraph
  },
  {
    title: 'Usage guides',
    paragraph
  },
  {
    title: 'Purchasing process',
    paragraph
  },
  {
    title: 'Usage guides',
    paragraph
  }
]
 
export class Accordion extends React.Component {
  render () {
    return (
      <div {...{ className: 'wrapper' }}>
        <div className="text">
            <h3>Популярные вопросы</h3>
        <div>
            <div style={{display: "flex", gap: "10px"}}>
              <a href="#projects" className="button1">
                Инвестиции
              </a>
              <a href="#projects" className="button2">
                ЗПИФ
              </a>  
            </div>
        
        </div>
        <p>Больше полезной информации вы можете узнать в нашем информационном портале</p>
        <button className="button3">Гид для инвесторов <svg width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg">
				<path fill-rule="evenodd" clip-rule="evenodd" d="M4.66675 4C4.48992 4 4.32033 4.07025 4.19529 4.19529C4.07025 4.32033 4 4.48992 4 4.66675C4 4.84358 4.07025 5.01317 4.19529 5.13821C4.32033 5.26325 4.48992 5.3335 4.66675 5.3335H9.67575L4.19525 10.862C4.07283 10.9875 4.0048 11.1562 4.00589 11.3315C4.00699 11.5069 4.07712 11.6747 4.20109 11.7987C4.32507 11.9226 4.4929 11.9928 4.66822 11.9939C4.84354 11.995 5.01224 11.9269 5.13775 11.8045L10.6663 6.227V11.3335C10.6663 11.5103 10.7365 11.6799 10.8615 11.805C10.9866 11.93 11.1562 12.0002 11.333 12.0002C11.5098 12.0002 11.6794 11.93 11.8045 11.805C11.9295 11.6799 11.9998 11.5103 11.9998 11.3335V4.6665C11.9998 4.579 11.9826 4.49234 11.9492 4.41148C11.9158 4.33062 11.8667 4.25714 11.8049 4.19524C11.743 4.13335 11.6696 4.08424 11.5887 4.05074C11.5079 4.01724 11.4213 4 11.3337 4H4.66675Z" fill="#012DE7"></path>
			</svg></button>
        </div>
        

        <ul {...{ className: 'accordion-list' }}>
          {data.map((data, key) => {
            return (
              <li {...{ className: 'accordion-list__item', key }}>
                <AccordionItem {...data} />
              </li>
            )
          })}
        </ul>
      </div>
    )
  }
}

class AccordionItem extends React.Component {
  state = {
    opened: false
  }
  
  render () {
    const {
      props: {
        paragraph,
        title
      },
      state: {
        opened
      }
    } = this
    
    return (
      <div
        {...{
          className: `accordion-item, ${opened && 'accordion-item--opened'}`,
          onClick: () => { this.setState({ opened: !opened }) }
        }}
      >
        <div {...{ className: 'accordion-item__line' }}>
            <span {...{ className: 'accordion-item__icon' }}/>
          <h3 {...{ className: 'accordion-item__title' }}>
            {title}
          </h3>
          
        </div>
          <div {...{ className: 'accordion-item__inner' }}>
            <div {...{ className: 'accordion-item__content' }}>
              <p {...{ className: 'accordion-item__paragraph' }}>
                {paragraph}
              </p>
            </div>
          </div>
      </div>
    )
  }
}